import { AuthContext } from "@/Context/AuthContext";
import { use } from "react";
import { FaRegUser } from "react-icons/fa";

export default function forgotPassword() {
  const { user } = use(AuthContext);

  return (
    <div className="flex justify-center items-center py-10">
      <form
        className="bg-white shadow-lg px-6 py-5 rounded w-[300px]"
        // onSubmit={handleLogin}
      >
        {/* name */}
        <label className="label text-black font-semibold mb-3">Name</label>
        <div className="flex items-center px-3 rounded mb-2 bg-[#F8F7FB]">
          <FaRegUser className="text-primary" />
          <input
            type="text"
            className="input border-0 outline-0 bg-transparent"
            placeholder="Name"
            name="name"
          />
        </div>
      </form>
    </div>
  );
}
